/* Global Styles */
//@import 'styles/main';
@import 'styles/modal';
@import 'styles/abstracts/variables';
@import 'styles/abstracts/mixins';
@import url('https://fonts.googleapis.com/css?family=Comfortaa|Nanum+Pen+Script&display=swap');

//font-family: 'Comfortaa', cursive;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  //font-family: 'Raleway', sans-serif, 'Haas Grot Text R Web', 'Helvetica Neue',Helvetica, Arial;
  //font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 0.6rem;
  line-height: 1.6;
  background-color: #fff;
  color: #444;
  overflow-x: auto;
  padding: 10px;
  //background: var(--main-body-bg-color);
}
.modal-body {
  font-size: 0.8rem;
}
.form-info-text {
  font-size: 0.875rem !important;
  padding-left: 7px;
}
.form-title-text {
  color: #444 !important;
  padding-left: 7px;
}

a {
  color: var(--primary-color);
  text-decoration: none;
  .item {
    color: '#333';
  }
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Text Styles*/
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 0.8rem;
}

.large {
  font-size: 2rem;
  line-height: 1.2;
  margin-bottom: 0.8rem;
}

.lead {
  font-size: 1.2rem;
  margin-bottom: 0.8rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: var(--large-text-color);
}

.text-dark {
  color: var(--dark-color);
}

/* Padding */
.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

/* Margin */
.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

.m-left-5 {
  margin-left: 5px;
}
.m-top-20 {
  margin-top: 20px !important;
}

.m-bottom-15 {
  margin-bottom: 15px !important;
}

.btn {
  display: inline-block;
  background: var(--light-color);
  color: #333;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.badge {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: center;
  margin: 0.3rem;
  background: var(--light-color);
  color: #333;
}

.alert {
  padding: 0.8rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: var(--light-color);
  color: #333;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(var(--bg-gradient1)),
    to(var(--bg-gradient2))
  ) !important;
  background-image: linear-gradient(
    90deg,
    var(--bg-gradient1),
    var(--bg-gradient2)
  ) !important;

  color: #fff;
  border: none;
  text-decoration: none;
  -webkit-transition: color 200ms ease;
  transition: color 200ms ease;
  box-sizing: border-box;
  border-radius: 0;
  background: var(--bg-primary-btn);

  &:hover,
  &:active,
  &:focus {
    color: var(--color-primary-btn-hover);
    background: var(--bg-primary-btn-hover) !important;
  }
}
