.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333; }

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff; }

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff; }

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff; }

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: var(--white-color);
  color: #333; }

.btn:hover {
  opacity: 0.8; }

.bg-light,
.badge-light {
  border: #ccc solid 1px; }

.round-img {
  border-radius: 50%; }

.line {
  height: 1px;
  background: #ccc;
  margin: 1.5rem 0; }

/* Overlay */
.dark-overlay {
  /*background-color: rgba(0, 0, 0, 0.7);
  position: absolute;*/
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

/* Forms */
.form .form-group {
  margin: 1.2rem 0; }

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888; }

.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 0.8rem;
  border: 1px solid #ccc; }

.form input[type='submit'],
button {
  font: inherit; }

.table th,
.table td {
  padding: 1rem;
  text-align: left; }

.table th {
  background: var(--light-color); }

/* Landing Page */
.landing {
  position: relative;
  background: url("./img/show.png") no-repeat center center/cover;
  height: 100vh; }

.landing-inner {
  color: #1d1d1d;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; }

/* Profiles Page */
.profile {
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  align-items: center;
  grid-gap: 2rem;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem; }

/* Profile Page */
.profile-grid {
  display: grid;
  grid-template-areas: 'top top' 'about about' 'exp edu' 'github github';
  grid-gap: 1rem; }

.profile-top {
  grid-area: top;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; }

.profile-top img {
  width: 50px; }

.profile-top .icons a {
  color: #fff;
  margin: 0 0.3rem; }

.profile-top .icons a:hover {
  color: var(--dark-color); }

.profile-about {
  grid-area: about;
  text-align: center; }

.profile-about .interests {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center; }

/* Mobile Styles */
@media (max-width: 700px) {
  .container {
    margin-top: 8rem; }
  .hide-sm {
    display: none; }
  /* Text Styles */
  .x-large {
    font-size: 3rem; }
  .large {
    font-size: 2rem; }
  .lead {
    font-size: 0.8rem; }
  /* Navbar */
  .navbar {
    display: block;
    text-align: center; }
  .navbar ul {
    text-align: center;
    justify-content: center; }
  .navbar h1 {
    margin-bottom: 1rem; }
  .navbar .welcome {
    display: none; }
  /* Profiles Page */
  .profile {
    grid-template-columns: 1fr;
    text-align: center; }
  .profile ul {
    display: none; }
  /* Profile Page */
  .profile-top img,
  .profile img {
    width: 200px;
    margin: auto; }
  .profile-grid {
    grid-template-areas: 'top' 'about' 'exp' 'edu' 'github'; }
  .profile-about .interests {
    flex-direction: column; }
  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem; }
  .post {
    grid-template-columns: 1fr; }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem; } }

.post-create-textarea {
  -moz-appearance: textfield-multiline;
  -webkit-appearance: textarea;
  color: #676e7a;
  font: medium -moz-fixed;
  font: -webkit-small-control;
  height: 40px;
  overflow: auto;
  padding: 2px;
  resize: both;
