.logo {
  width: 150px;
  line-height: 30px;
  float: left; }

.logo a {
  display: inline-block;
  font-size: 12px;
  font-weight: 400;
  padding: 10px 15px;
  text-transform: capitalize; }

.menuCon {
  width: calc(100% - 150px);
  float: left; }

.menuCon .ant-menu-item {
  padding: 0px 5px; }

.menuCon .ant-menu-submenu-title {
  padding: 10px 20px; }
  .menuCon .ant-menu-submenu-title span {
    color: #45b3e0 !important;
    color: var(--primary-color) !important; }

.menuCon .ant-menu-item a,
.menuCon .ant-menu-submenu-title a {
  padding: 10px 15px;
  font-size: 12px;
  font-weight: 400; }

.menuCon .ant-menu-horizontal {
  border-bottom: none; }

.menuCon .leftMenu {
  float: left; }

.menuCon .rightMenu {
  float: right; }

.barsMenu {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none;
  background: none; }

.barsBtn {
  display: block;
  width: 20px;
  height: 2px;
  position: relative; }

.barsBtn:after,
.barsBtn:before {
  content: attr(x);
  width: 20px;
  position: absolute;
  top: -6px;
  left: 0;
  height: 2px;
  background: #45b3e0;
  background: var(--bg-primary-btn);
  color: #fff; }

.barsBtn:after {
  top: auto;
  bottom: -6px; }

.ant-drawer-body {
  padding: 0; }

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item,
.ant-drawer-body .ant-menu-horizontal > .ant-menu-submenu {
  display: inline-block;
  width: 100%; }

.ant-drawer-body .ant-menu-horizontal {
  border-bottom: none; }

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item:hover {
  border-bottom-color: transparent; }

@media (max-width: 767px) {
  .barsMenu {
    display: inline-block; }
  .leftMenu,
  .rightMenu {
    display: none; }
  .logo a {
    margin-left: -20px; }
  .menuCon .ant-menu-item,
  .menuCon .ant-menu-submenu-title {
    padding: 1px 20px; }
  .logo a {
    padding: 10px 20px; } }

